import React, { useState, useEffect } from "react";
import { useLocation, useNavigate } from "react-router-dom";
import { dbService } from "../fbase";
import { getAuth } from "firebase/auth";
import {
  collection,
  addDoc,
  query,
  onSnapshot,
  doc,
  orderBy,
  updateDoc,
  increment,
  getDoc,
} from "firebase/firestore";
import styles from "./css/Write.module.css";
import like from "../assets/like.png";
import dislike from "../assets/dislike.png";
import { faL } from "@fortawesome/free-solid-svg-icons";

const Write = ({ userObj }) => {
  const { state } = useLocation();
  const [title, setTitle] = useState("");
  const dateObj = new Date();
  const today = `${dateObj.getFullYear()}/${dateObj.getMonth()}/${dateObj.getDate()}`;
  const [review, setReview] = useState(""); //review , form
  const [reviews, setReviews] = useState([]); //review  
  const [recommend, setRecommend] = useState(true);
  console.log("state: ", state);

  const auth = getAuth();
  const userRef = doc(dbService, "UserInfo", `${auth.currentUser.uid}`);

  const navigate = useNavigate();

  //    1depth  
  const categoryName = state.categoryName;
  console.log("categoryName: ", categoryName);
  const cnSplit = categoryName.split(">");
  const genre = cnSplit[1];
  console.log("genre: ", genre);

  useEffect(() => {
    const q = query(
      collection(dbService, "reviews"), //collection "reviwes" 
      orderBy("createdAt", "desc") //
    );
    onSnapshot(q, (snapshot) => {
      //     
      const reviewArr = snapshot.docs.map((document) => ({
        id: document.id,
        ...document.data(),
      }));
      setReviews(reviewArr); //  state   
    });
  }, []);

  const onSubmit = (e) => {
    e.preventDefault();
    const title = e.target[0].value;
    const date = e.target[1].value;
    const star = e.target[2].value;
    const review = e.target[3].value;
    const bookimg = state.cover;
    const author = state.author;

    //reviews  doc    userObj.uid   ?
    const docRef = addDoc(collection(dbService, "reviews"), {
      text: review, // 
      createdAt: dateObj, //  
      creatorId: userObj.uid, //   
      title: title, // 
      star: star, //  
      bookimg: bookimg, //  
      author: author, //
      genre: genre,
    });

    switch (String(genre)) {
      case "/":
        updateDoc(userRef, {
          genre1: increment(3),
        });
        break;
      case "/":
        updateDoc(userRef, {
          genre2: increment(3),
        });
        break;
      case "":
        updateDoc(userRef, {
          genre3: increment(3),
        });
        break;
      case "":
        updateDoc(userRef, {
          genre4: increment(3),
        });
        break;
      case "":
        updateDoc(userRef, {
          genre5: increment(3),
        });
        break;
      case "":
        updateDoc(userRef, {
          genre6: increment(3),
        });
        break;
      case "/":
        updateDoc(userRef, {
          genre7: increment(3),
        });
        break;
      case "/":
        updateDoc(userRef, {
          genre8: increment(3),
        });
        break;
      case "":
        updateDoc(userRef, {
          genre9: increment(3),
        });
        break;
      case "":
        updateDoc(userRef, {
          genre10: increment(3),
        });
        break;
      case "//":
        updateDoc(userRef, {
          genre11: increment(3),
        });
        break;
      case "/":
        updateDoc(userRef, {
          genre12: increment(3),
        });
        break;
      case "":
        updateDoc(userRef, {
          genre13: increment(3),
        });
        break;
      case "":
        updateDoc(userRef, {
          genre14: increment(3),
        });
        break;
      case "":
        updateDoc(userRef, {
          genre15: increment(3),
        });
        break;
      case "":
        updateDoc(userRef, {
          genre16: increment(3),
        });
        break;
      case "/":
        updateDoc(userRef, {
          genre17: increment(3),
        });
        break;
      case "":
        updateDoc(userRef, {
          genre18: increment(3),
        });
        break;
      case "":
        updateDoc(userRef, {
          genre19: increment(3),
        });
        break;
      case "":
        updateDoc(userRef, {
          genre20: increment(3),
        });
        break;
      case "":
        updateDoc(userRef, {
          genre21: increment(3),
        });
        break;
      case "":
        updateDoc(userRef, {
          genre22: increment(3),
        });
        break;
      case "":
        updateDoc(userRef, {
          genre23: increment(3),
        });
        break;
      case "/":
        updateDoc(userRef, {
          genre24: increment(3),
        });
        break;
      case "/":
        updateDoc(userRef, {
          genre25: increment(3),
        });
        break;
      case "":
        updateDoc(userRef, {
          genre26: increment(3),
        });
        break;
      case "":
        updateDoc(userRef, {
          genre27: increment(3),
        });
        break;
      case "":
        updateDoc(userRef, {
          genre28: increment(3),
        });
        break;
      case "":
        updateDoc(userRef, {
          genre29: increment(3),
        });
        break;
      case "/":
        updateDoc(userRef, {
          genre30: increment(3),
        });
        break;
      default:
        updateDoc(userRef, {
          minor: increment(1),
        });
        break;
    }

    setReview("");
    navigate("/library/*");
  };

  const onChange = (e) => {
    const {
      target: { value, name },
    } = e;
    if (name === "title") {
      setTitle(value);
    }
    if (name === "review") {
      setReview(value); // 
    }
  };

  return (
    <div className={styles.wrapper}>
      <section>
        <div className={styles.sectionTop}>
          <img src={state.cover} className={styles.coverImg} />
          <h2>{state.title}</h2>
          <span> {state.author}</span>
        </div>
        <div className={styles.sectionBottom}>
          <div>{state.description}</div>
        </div>
      </section>

      <form id="writeForm" onSubmit={onSubmit} className={styles.writeForm}>
        <div className={styles.title}>
          <label for="title"> </label>
          <input
            type="text"
            placeholder={state ? `${state.title}` : ""}
            value={state ? `${state.title}` : `${title}`}
            onChange={onChange}
            name="title"
            id="title"
          ></input>
        </div>

        <div className={styles.title}>
          <label for="date"></label>
          <input type="date" name="date" id="date"></input>
        </div>

        <div className={styles.title}>
          <label for="star"></label>
          <select name="star">
            <option value=""></option>
            <option value="1">1</option>
            <option value="2">2</option>
            <option value="3">3</option>
            <option value="4">4</option>
            <option value="5">5</option>
          </select>
        </div>

        <div className={styles.title}>
          <label for="review"></label>
          <textarea name="review" form="writeForm"></textarea>
        </div>

        <input className={styles.Btn} type="submit" value=""></input>
      </form>
    </div>
  );
};

/*      <div className={styles.recommendSection}>
<label>Recommend</label>
<img
  src={like}
  className={`${styles.recommend} ${styles.Btn2} ${styles.setRecommend}`}
  name="recommend"
/>
<img
  src={dislike}
  className={`${styles.unrecommend} ${styles.Btn2}`}
  name="unrecommend"
/>
</div>
*/
export default Write;
