import { async } from '@firebase/util';
import React, { useEffect, useState } from 'react';
import { getDownloadURL, ref, uploadString } from '@firebase/storage';
import { dbService, storageService } from '../fbase';
import {
  collection,
  addDoc,
  query,
  onSnapshot,
  orderBy,
  serverTimestamp,
} from 'firebase/firestore';
import { useRef } from 'react';
import { v4 } from 'uuid';

import styles from '../routes/css/BookNeighbor.module.css';
import Top2 from './Top2';
import { useLocation, useNavigate } from 'react-router-dom';
import { getAuth } from 'firebase/auth';

const WriteLent = ({ userObj }) => {
  const [title, setTitle] = useState('');
  const [lentContent, setLentContent] = useState('');
  const [lentContents, setLentContents] = useState([]);
  const [location, setLocation] = useState(-1); //location  string  number ?
  const [imgfile, setImgfile] = useState('');
  const [comments, setComments] = useState([]); //  
  const [available, setAvailable] = useState(true); //      !
  const [state, setState] = useState({ size: 1 });
  const navigate = useNavigate();

  const auth = getAuth();

  useEffect(() => {
    const q = query(
      collection(dbService, 'lentContents'), //collection 'lentContents' 
      orderBy('createdAt', 'desc') //
    );
    onSnapshot(q, (snapshot) => {
      const lentContentArr = snapshot.docs.map((document) => ({
        id: document.id,
        ...document.data(),
      }));
      setLentContents(lentContentArr);
    });
  }, []);

  //   : title, content, location(number), imgfile
  const onSubmit = async (event) => {
    event.preventDefault();
    const title = event.target[0].value;
    const lentContent = event.target[1].value;
    const location = event.target[2].value;
    await addDoc(collection(dbService, 'lentContents'), {
      title: title, //  (    )
      content: lentContent, //  :   
      location: location, //  
      imgfile: imgfile, // 
      creatorId: userObj.uid, //  
      createdAt: serverTimestamp(),
      comments: comments,
      available: available,
    });
    setLentContent(''); //navigate   
    navigate('/mypage', {
      state: {
        pagenum: 1,
      },
    }); //LendList    
  };

  const onChange = (event) => {
    const {
      target: { name, value },
    } = event;
    if (name === 'title') {
      setTitle(value);
      return;
    }
    if (name === 'content') {
      setLentContent(value);
      return;
    }
    if (name === 'location') {
      setLocation(value);
    }
  };

  //nwitter    ,,   ok
  const onFileChange = (event) => {
    const {
      target: { files },
    } = event;
    const image = files[0];
    const reader = new FileReader();
    reader.onloadend = (finishedEvent) => {
      const {
        currentTarget: { result },
      } = finishedEvent;
      setImgfile(result);
    };
    reader.readAsDataURL(image);
  };

  //remove   
  const onClearImg = (event) => {
    setImgfile('');
  };

  return (
    <div className={styles.writeLent_Container}>

      <div className={styles.writeLent_TopContainer}>
        <span
          className={styles.writeLent_TopContainer_Content}
        >{`(  )`}</span>
      </div>
      <section>
        <form
          id="lentForm"
          onSubmit={onSubmit}
          className={styles.writeLent_form}
        >
          <div className={styles.addImg}>
            {!imgfile && (
              <>
                <label htmlFor="attach_file">
                  <span className={styles.addImg}>+</span>
                </label>
                <input
                  id="attach_file"
                  type="file"
                  accept="image/*"
                  onChange={onFileChange}
                  style={{ display: 'none' }}
                ></input>
              </>
            )}
            {imgfile && (
              <div className={styles.attached_img}>
                <img src={imgfile}></img>
                <div onClick={onClearImg}>
                  <span>Remove</span>
                </div>
              </div>
            )}
          </div>
          <div className={styles.writeLent_Title}>
            <label htmlFor="title"></label>
            <input
              id="title"
              type="text"
              placeholder=" "
              value={title}
              name="title"
              onChange={onChange}
            ></input>
          </div>
          <div className={styles.writeLent_Contents}>
            <label htmlFor="content"></label>
            <textarea
              id="content"
              form="lentForm"
              name="content"
              placeholder="  ,    !"
            ></textarea>
          </div>
          <div className={styles.writeLent_Loc}>
            <label htmlFor="selectLoc"></label>
            <select
              id="selectLoc"
              className={styles.selectLoc}
              size={state.size}
              onFocus={() => {
                setState({ size: 5 });
              }}
              onBlur={() => {
                setState({ size: 1 });
              }}
              onChange={(e) => {
                e.target.blur();
              }}
            >
              <option disabled selected>
                
              </option>
              <option value="1"></option>
              <option value="2"></option>
              <option value="3"></option>
              <option value="4"></option>
              <option value="5"></option>
              <option value="6"></option>
              <option value="7"></option>
              <option value="8"></option>
              <option value="9"></option>
              <option value="10"></option>
              <option value="11"></option>
              <option value="12"></option>
              <option value="13"></option>
              <option value="14"></option>
              <option value="15"></option>
              <option value="16"></option>
              <option value="17"></option>
              <option value="18"></option>
              <option value="19"></option>
              <option value="20"></option>
              <option value="21"></option>
              <option value="22"></option>
              <option value="23"></option>
              <option value="24"></option>
              <option value="25"></option>
            </select>
          </div>

          <input
            type="submit"
            value=""
            className={styles.writeLent_SubmitBtn}
          ></input>
        </form>
      </section>
    </div>
  );
};

export default WriteLent;
