import React, { useEffect, useState, memo } from 'react';
import { useNavigate, useLocation } from 'react-router-dom';
import styles from '../routes/css/BookNeighbor.module.css';
import { getAuth } from 'firebase/auth';
import { deleteDoc, doc, updateDoc, serverTimestamp } from 'firebase/firestore';
import { dbService } from '../fbase';

import Comment from './Comment';
import { async } from '@firebase/util';

const LIMIT = 100;

const NeighborContent = ({ content }) => {
  const [limit, setLimit] = useState(LIMIT);
  const [region, setRegion] = useState('');
  const time = String(Date(content.createdAt)).split(' ');
  const timestr = `${time[3]}/${time[1]}/${time[2]}/${time[0]}`;
  const navigate = useNavigate();

  const summary = content.content.slice(0, LIMIT);
  const regionNum = content.location;

  useEffect(() => {
    switch (Number(regionNum)) {
      case 1:
        setRegion('');
        break;
      case 2:
        setRegion('');
        break;
      case 3:
        setRegion('');
        break;
      case 4:
        setRegion('');
        break;
      case 5:
        setRegion('');
        break;
      case 6:
        setRegion('');
        break;
      case 7:
        setRegion('');
        break;
      case 8:
        setRegion('');
        break;
      case 9:
        setRegion('');
        break;
      case 10:
        setRegion('');
        break;
      case 11:
        setRegion('');
        break;
      case 12:
        setRegion('');
        break;
      case 13:
        setRegion('');
        break;
      case 14:
        setRegion('');
        break;
      case 15:
        setRegion('');
        break;
      case 16:
        setRegion('');
        break;
      case 17:
        setRegion('');
        break;
      case 18:
        setRegion('');
        break;
      case 19:
        setRegion('');
        break;
      case 20:
        setRegion('');
        break;
      case 21:
        setRegion('');
        break;
      case 22:
        setRegion('');
        break;
      case 23:
        setRegion('');
        break;
      case 24:
        setRegion('');
        break;
      case 25:
        setRegion('');
        break;
      default:
        break;
    }
  });

  const onClick = (e) => {
    navigate('/bookneighbor/neighborContent', {
      state: {
        region: region,
        timestr,
        content: content.content,
        title: content.title,
        img: content.imgfile,
        id: content.id,
        uid: content.cid,
        page: 'bookneighbor',
        comments: content.comments,
        available: content.available,
      },
    });
    console.log(content);
  };

  return (
    <div>
      <div className={styles.content} onClick={onClick}>
        <img src={content.imgfile} className={styles.userImg} />
        <div>
          <div className={styles.content_title}>
            {content.title}
            <span
              style={{
                marginLeft: '5px',
                fontSize: '10px',
                fontWeight: '400',
                color: '#a8a8a8',
              }}
            >
              {region}
            </span>
          </div>
          <div className={styles.date}>{timestr}</div>
          {content.content.length < LIMIT ? (
            <p className={styles.content_detail}>{content.content}</p>
          ) : (
            <>
              <p className={styles.content_detail}>{summary}...</p>
            </>
          )}
        </div>
      </div>
    </div>
  );
};

const BookNeighborDetailPage = () => {
  const [location, setLocation] = useState(useLocation());
  const [region, setRegion] = useState(location.state.region);
  const [timestr, setTimestr] = useState(location.state.timestr);
  const [content, setContent] = useState(location.state.content);
  const [title, setTitle] = useState(location.state.title);
  const [img, setImg] = useState(location.state.img);
  const [uid, setUid] = useState(location.state.uid);
  const [comments, setComments] = useState(location.state.comments);

  const [page, setPage] = useState(location.state.page); //    

  const auth = getAuth();
  const userobj = auth.currentUser;

  //  
  const [edit, setEdit] = useState(false);
  const [state, setState] = useState({ size: 1 });
  const [locNum, setLocNum] = useState(-1);

  //, 
  const [possible, setPossible] = useState(location.state.available);
  const navigate = useNavigate();

  const contentRef = doc(dbService, 'lentContents', `${location.state.id}`);

  // 
  const onDeleteClick = async () => {
    const ok = window.confirm(' ?');
    if (ok) {
      try {
        await deleteDoc(contentRef);
        if (page === 'bookneighbor') {
          navigate('/bookneighbor');
        } else if (page === 'profile') {
          navigate('/mypage');
        }
      } catch (error) {
        window.alert(' ');
      }
    }
  };

  const onToggleEdit = () => {
    setEdit((prev) => !prev);
  };

  //
  const onEdit = async (event) => {
    event.preventDefault();
    await updateDoc(contentRef, {
      content: content,
      title: title,
      location: locNum,
      createdAt: serverTimestamp(),
    });
    setEdit(false);
  };

  const onChange = (event) => {
    const {
      target: { name, value },
    } = event;

    if (name === 'title') {
      setTitle(value);
      return;
    }
    if (name === 'content') {
      setContent(value);
      return;
    }
    if (name === 'locationNum') {
      setLocNum(value);
    }
  };

  //, 
  const onPossible = async (event) => {
    await updateDoc(contentRef, {
      available: !possible,
    });
    setPossible((prev) => !prev);
  };

  return (
    <div className={styles.bookNeighborDetailPage}>
      {edit ? (
        <div className={styles.editContainer}>
          <img src={img}></img>
          <div className={styles.editForm}>
            <form id="lentEditForm">
              <div>
                <label htmlFor="title"></label>
                <input
                  type="text"
                  value={title}
                  id="title"
                  onChange={onChange}
                  name="title"
                ></input>
              </div>
              <div>
                <label htmlFor="content"></label>
                <textarea
                  id="content"
                  form="lentEditForm"
                  name="content"
                  value={content}
                  onChange={onChange}
                ></textarea>
              </div>
              <div className={styles.writeLent_Loc}>
                <label htmlFor="selectLoc"></label>
                <select
                  id="selectLoc"
                  className={styles.selectLoc}
                  size={state.size}
                  onFocus={() => {
                    setState({ size: 5 });
                  }}
                  onBlur={() => {
                    setState({ size: 1 });
                  }}
                  onChange={(e) => {
                    e.target.blur();
                  }}
                >
                  <option disabled selected>
                    
                  </option>
                  <option value="1"></option>
                  <option value="2"></option>
                  <option value="3"></option>
                  <option value="4"></option>
                  <option value="5"></option>
                  <option value="6"></option>
                  <option value="7"></option>
                  <option value="8"></option>
                  <option value="9"></option>
                  <option value="10"></option>
                  <option value="11"></option>
                  <option value="12"></option>
                  <option value="13"></option>
                  <option value="14"></option>
                  <option value="15"></option>
                  <option value="16"></option>
                  <option value="17"></option>
                  <option value="18"></option>
                  <option value="19"></option>
                  <option value="20"></option>
                  <option value="21"></option>
                  <option value="22"></option>
                  <option value="23"></option>
                  <option value="24"></option>
                  <option value="25"></option>
                </select>
              </div>
            </form>
            <div id={styles.editBtn}>
              <button onClick={onToggleEdit}></button>
              <button onClick={onEdit}></button>
            </div>
          </div>
        </div>
      ) : (
        <>
          <img src={img} />
          <h1>{title}</h1>
          <div>
            <span>{region}</span>
            <span>{timestr}</span>
            {possible ? (
              <button
                className={`${styles.lentcheckBtn} ${styles.possible}`}
                disabled={userobj.uid === uid ? false : true}
                onClick={onPossible}
              >
                
              </button>
            ) : (
              <button
                disabled={userobj.uid === uid ? false : true}
                onClick={onPossible}
                className={`${styles.lentcheckBtn} ${styles.impossible}`}
              >
                
              </button>
            )}
          </div>
          <p>{content}</p>
          {userobj.uid === uid ? (
            <div className={styles.editDeleteBtn}>
              <button onClick={onToggleEdit}></button>
              <button onClick={onDeleteClick}></button>
            </div>
          ) : (
            <></>
          )}
          <Comment user={userobj} content={location} />
        </>
      )}
    </div>
  );
};

export { BookNeighborDetailPage };

export default NeighborContent;
